#mypage-review-wrapper {
    width: 1330px;
    gap: 45px;
}

#mypage-review-wrapper .title {
    text-align: center;
    margin-top: 70px;
    font-size: 30px;
}

#mypage-review-wrapper .main-title {
    font-size: 25px;
    font-weight: 600;

    margin-bottom: 15px;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

#mypage-review-wrapper .main-title .delete-icon {
    margin-top: 20px;
}

#mypage-review-wrapper .main-title .title-span:hover {
    cursor: pointer;
    text-decoration: underline;
}

#mypage-review-wrapper .review-box {
    display: flex;
    flex-direction: row;
}

.review-delete {
    border: 2px solid #555;
    width: 7%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    background-color: rgba(64, 64, 64, 0.5);
}


/**  start ================================================================  */
#mypage-review-wrapper .myreview-modal {
    position: fixed;
    top: 0px;
    left: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100vw;
    height: 100vh;

    background-color: rgba(0, 0, 0, 0.2);
}

#mypage-review-wrapper .modal-box {
    width: 800px;
    height: 500px;

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
}

#mypage-review-wrapper .slider-image-container {
    width: 500px;
    height: 500px;

    background-color: thistle;
}

#mypage-review-wrapper .arrow-left {
    background-image: url(/public/round-left.png);
}

#mypage-review-wrapper .arrow-right {
    background-image: url(/public/round-right.png);
}

#mypage-review-wrapper .arrow-left,
#mypage-review-wrapper .arrow-right {
    width: 40px;
    height: 40px;

    background-repeat: no-repeat;

    cursor: pointer;
}