#qa-write-wrapper {
    display: flex;
    flex-direction: column;
    gap: 25px;

    width: 1300px;
}

#qa-write-wrapper .top {
    margin-top: 40px;
}

#qa-write-wrapper .top,
#qa-write-wrapper .main,
#qa-write-wrapper .bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

#qa-write-wrapper .main textarea {
    height: 400px;
}

#qa-write-wrapper .subject,
#qa-write-wrapper .script {
    width: 100px;

    text-align: center;
    font-size: 18px;
    font-weight: 700;
}

#qa-write-wrapper input,
#qa-write-wrapper .main textarea {
    flex: 0.95;
    border-radius: 5px;
    padding: 8px 10px;

    border: 2px solid rgba(219, 219, 219, 1);

    outline: none;
}

#qa-write-wrapper .bottom {
    gap: 10px;
    justify-content: end;
}

#qa-write-wrapper .cancle-button {
    background-color: var(--white-color);
    color: var(--black-color);

    border: 1px solid rgba(100, 100, 100, 1);
    width: 80px;
    /* line-height: 20px; */
    padding: 10px;
    font-weight: 500;
}

#qa-write-wrapper .write-button {
    background-color: var(--white-color);
    color: var(--black-color);

    border: 1px solid black;
    width: 80px;
    /* line-height: 20px; */
    padding: 10px;
    background-color: black;
    color: white;
    font-weight: 500;
}

#qa-write-wrapper .cancle-button:hover {
    background-color: rgba(0, 0, 0, 0.7);
    border: 1px solid rgba(0, 0, 0, 0.7);
    color: white;
}

#qa-write-wrapper .write-button:hover {
    background-color: rgba(0, 0, 0, 0.7);
    border: 1px solid rgba(0, 0, 0, 0.7);
}