.pagination-box {
    display: flex;
    align-items: center;
    gap: 24px;
}

.left-button,
.right-button {
    line-height: 50px;
}

.left-button {
    width: 40px;
    height: 40px;

    /* background-image: url(/public//images/round-left.png); */
    background-size: 100% 100%;

    cursor: pointer;
}

.right-button {
    width: 40px;
    height: 40px;

    /* background-image: url(/public//images/round-right.png); */
    background-size: 100% 100%;

    cursor: pointer;
}

.page-list {
    display: flex;
    gap: 16px;
}

.page {
    color: rgba(138, 146, 166, 1);

    font-size: 18px;
    font-weight: 400;
    line-height: 20px;

    cursor: pointer;
}

.page-active {
    color: black;

    font-weight: 700;
    cursor: default;
}